import { BrowserRouter, HashRouter , Route, Navigate, Routes } from 'react-router-dom';
import "./index.css";
import React from "react";
import Home from "./Pages/Home";
import ResponseGood from "./Pages/ResponseGood";
import ResponseBad from "./Pages/ResponseBad";
function App() {
  return (
    <HashRouter>
      <Routes>
      <Route exact path="/" element={<Home />} />
      <Route
        exact
        path="/goodresponse"
        element={<ResponseGood />}
      />
      <Route
        exact
        path="/badresponse"
        element={<ResponseBad />}
      />
      </Routes>
    </HashRouter>
  );
}

export default App;
